import ApiService from "@/core/services/api.service";
import JwtService from "@/core/services/jwt.service";

// Endpoints
const resource = "/Currency";
const exchangeRateService = {
  get() {
    if (JwtService.getToken()) {
      ApiService.setHeader();
      return ApiService.get(resource, "exchangeRate");
    }
  },

  getById(key) {
    if (JwtService.getToken()) {
      ApiService.setHeader();
      return ApiService.get(resource, `${key}/exchangeRate`);
    }
  },

  create(data) {
    if (JwtService.getToken()) {
      ApiService.setHeader();
      return ApiService.post(`${resource}/exchangeRate`, data);
    }
  },

  update(key, data) {
    if (JwtService.getToken()) {
      ApiService.setHeader();
      return ApiService.update(resource, `${key}/exchangeRate`, data);
    }
  },

  delete(key) {
    if (JwtService.getToken()) {
      ApiService.setHeader();
      return ApiService.delete(resource, `${key}/exchangeRate`);
    }
  }
};

export default exchangeRateService;
